.container {
  background-color: white;
  min-height: 2em;
  border: 1px solid #eee;
  color: black;
  font-family: "Helvetica Neue";
  font-weight: lighter;

  input[type="radio"]:checked {
    color: gray;
  }
  .taskname {
    padding: 0.5em 0 0 0.5em;
  }

  .taskdone {
    text-decoration: line-through;
  }
}